*{
    margin: 0;
    padding: 0;
}
.flex{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
}




.mainContainer{
    width: 45vw;
    margin-top: 5vh;
    height: 90vh;
    background-color: rgb(184, 247, 247);
    border-radius: 10px;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.13);
   
}


.questionContainer{
    width: 90%;
    height: 15%;
    background-color: white;
    font-weight: bold;
    color: black;
    border-radius: 10px;
    padding: 5px;
}
.optionContainer{
    margin-top: 5vh;
    width: 90%;
    height: 40%;
    justify-content: space-between;
    flex-direction: row;
   
    flex-wrap: wrap;
    
}
.options{
    height: 30%;
    background-color: white;
    border-radius: 10px;
    width: 45%;font-weight: bold;
padding: 2%;
    
}
img{
    height: 50%;
    width: 40%;
}
.options:hover{
    color: white;
    
    background: purple;
}
.btnContainer{
    width: 90%;
   flex-wrap: wrap;
    height: 60%; 
    flex-direction: row;
    justify-content: space-evenly;
    
}
 .heading{
        display: flex;
        flex-direction: column;
        justify-content: center;align-items: center;flex-wrap: wrap;
    }
.btn1{display: flex;
    width: 40%;
    height: 20%;
    border-radius: 4px;
  align-items: center;
  justify-content: center;
    background-color: green;
    border: 3px solid transparent;
    color: white;
   font-weight: bold;
   font-size: 25px;
   box-shadow: 2px 2px 20px rgba(0, 0, 0, 0.185);
  text-decoration: none;

    
}

.btn1:hover{
    background: rgb(247, 244, 244);
    color: black;

    border:3px solid green
}

.btnContainer2{
    width: 90%;
    flex-direction: row;
    height: 20%; 
    justify-content: space-between;
    
}
.btn{
    height: 50%;
    /* background-color: white; */
    border-radius: 4px;
  text-decoration: none;
    color: white;
   font-weight: bold;
   box-shadow: 2px 2px 20px rgba(0, 0, 0, 0.185);
    
}
#playAgain{
    width: 40%;
    background-color: green;
    border: 3px solid transparent;
}
#playAgain:hover{
    background: rgb(247, 244, 244);
    color: black;
    border:3px solid green
}
#share{
    width: 40%;
    background-color: blue;
    border: 3px solid transparent;
}
#share:hover{
    background-color: rgb(250, 252, 250);
    border:3px solid blue;
    color: black;
}

#scoreBox{
    width: 80%;
    height: 55%;
    margin: 50px;
    background-color: white;
    font-weight: bold;
    color: black;
    border-radius:8px;
    justify-content: space-around;
    align-items: center;
}
#scoreBox h2{
    font-size:10vw
}
#scoreBox h1{
    font-size:5vw
}
#play{
    width: 50%;
    background-color: green;
    border: 3px solid transparent;
}
#play:hover{
    background: rgb(247, 244, 244);
    color: black;

    border:3px solid green
}

#submit{
    width: 50%;
    background-color: green;
    border: 3px solid transparent;
}
#submit:hover{
    background: rgb(247, 244, 244);
    color: black;

    border:3px solid green
}

#back{
    width: 20%;
    background: red;
    border: 3px solid transparent;
}
#next{
    width: 20%;
    background-color: blue;
    border: 3px solid transparent;

}
#back:hover{
    background: rgb(247, 244, 244);
    color: black;

    border:3px solid red
}
#next:hover{
    background-color: rgb(250, 252, 250);
    border:3px solid blue;
    color: black;
}
@media only screen and (max-width: 1200px) {
   .mainContainer{
       width: 70vw;
   }
  }
  @media only screen and (max-width: 780px) {
    .mainContainer{
        width: 80vw;
       
    }
    h1{
        font-size: 35px;
    }
    span{
        font-size: 40px !important;
    }
    
   }
   @media only screen and (max-width: 650px) {
    .mainContainer{
        width: 85vw;
       
    }
    h1{
        font-size: 33px;
    }
    span{
        font-size: 37px !important;
    }
    
   }
  @media only screen and (max-width: 550px) {
    .mainContainer{
        width: 90vw;
       
    }
    h1{
        font-size: 30px  !important;
    }
    span{
        font-size: 34px !important;
    }
    
   }
   @media only screen and (max-width: 470px) {
    .mainContainer{
        width: 90vw;
       
    }
    h1{
        font-size: 25px  !important;
    }
    span{
        font-size: 30px !important;
    }
    
   }
   @media only screen and (max-width: 400px) {
    .mainContainer{
        width: 90vw;
       
    }
    h1{
        font-size: 20px  !important;
    }
    span{
        font-size: 25px !important;
    }
    
   }
   @media only screen and (max-width:330px) {
    .mainContainer{
        width: 90vw;
       
    }
    h1{
        font-size: 17px  !important;
    }
    span{
        font-size: 25px !important;
    }
    
   }
  
   
    
   